<template>
  <div>
      <div id="app">
        <label for="cname">Name</label>
        <input id="cname" type="text" v-model="ctryName"/>

        <label for="ccode">Code</label>
        <input id="ccode" type="text" v-model="ctryCode"/>

        <label for="ccapital">Capital</label>
        <input id="ccapital" type="text" v-model="ctryCapital" />

        <label for="cregion">Region</label>
        <input id="cregion" type="text" v-model="ctryRegion" />

        <label for="cpopulation">Population</label>
        <input id="cpopulation" type="text" v-model="ctryPopulation" />

        <button id="add" v-on:click="addMe()">Add Data</button>
      </div>
    
    <!-- Replace "My" at line 4 with your name -->
    <h2>My Firebase Experiment in VueJS</h2>
    <table>
        <tr><th>Name</th><th>Region</th><th>Action</th></tr>
        <template v-for="c in countryData">
            <tr v-bind:key="c.code">
                <td>{{c.name}}</td>
                <td>{{c.region}}</td>
                <td>
                    <button v-on:click="removeMe(c.fbKey)">Delete</button>
                </td>
            </tr>
        </template>
    </table>
  </div>
</template>
<script>
  /* the .js suffix is optional */
  import { MYDB } from "../myFirebaseInit";

  export default {
    /* What to write here? */
    data() {
      return {
        countryData: [],
        ctryName: "",
        ctryCode: "",
        ctryCapital: "",
        ctryRegion: "",
        ctryPopulation: ""
      };
    },
    methods: {
      addMe(){
          let targetRef = MYDB.ref('countries');
          targetRef.push().set({
              capital: `${this.ctryCapital}`,
              code: `${this.ctryCode}`,
              name: `${this.ctryName}`,
              population: `${this.ctryPopulation}`,
              region: this.ctryRegion
          })
      },
      removeMe(aKey){
        console.log("About to remove one record from Firebase", aKey);
        let targetRef = MYDB.ref(`countries/${aKey}`);
        targetRef.remove();
        for(let k in this.countryData)
        {
            if(this.countryData[k].fbKey == aKey)
            {
                this.countryData.splice(k,1);
            }
        }
      },
      dataHandler(snapshot) {
        this.countryData.push({ fbKey: snapshot.key, ...snapshot.val() });
      }
    },
    mounted() {
      MYDB.ref("countries").on("child_added", this.dataHandler);
    },

    beforeDestroy() {
      console.log("About to be destroyed", this);
      MYDB.ref("countries").off("child_added", this.dataHandler);
    }

  };
</script>
<style>
#app {
    display: grid;
    border: thin solid grey;
}
#add{
    grid-column-start: 2;
    grid-column-end: 3;
    justify-self: right;
}


</style>
